
















import './users.scss';
import { IComponent } from '@/core/interfaces/component.interface';
import { mapActions, mapGetters } from 'vuex';

export default {
  computed: {
    ...mapGetters(['users']),
  },
  async mounted() {
    const usersPage = this as unknown as IComponent;
    await usersPage.$store.dispatch('loadUsers');
  },
  methods: {
    ...mapActions(['loadRecords']),
  },
};
